/* iPhone 5 / 5S / SE */
@media only screen and (max-height: 320px) and (max-height: 568px) {
  .sliderTitle {
    font-size: 22px;
    width: 100%;
    height: 35px;
    overflow-x: scroll;
    overflow-y: hidden;
    scroll-behavior: smooth;
  }
  .sliderDate {
    font-size: 14px;
    width: 100%;
    height: 18px;
    overflow-x: scroll;
    scroll-behavior: smooth;
    margin-top: 0px;
  }
  .sliderVote {
    font-size: 14px;
    width: 100%;
    height: 18px;
    overflow-x: scroll;
    scroll-behavior: smooth;
    margin-top: -10px;
  }
  .thumbSlider {
    height: 30vmax;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    overflow-x: scroll;
  }
  .thumbCard {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding: 5px;
    width: 24vw;
    height: 20vmax;
  }
  .thumbImg {
    width: auto;
    height: 20vmax;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 10px;
  }
  .thumbTitle {
    font-size: 16px;
    width: 100%;
    height: 18px;
    overflow-y: scroll;
    scroll-behavior: smooth;
  }
  .thumbDate {
    font-size: 1.2vmax;
    margin-top: -1px;
  }
  .thumbVote {
    font-size: 1.5vmax;
    color: gold;
    margin-top: -1px;
  }
  .movieBackDrop {
    height: 35vh;
    width: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    position: relative;
  }
  .BackDropBox {
    height: 35vh;
    width: auto;
    -o-object-fit: cover;
    object-fit: cover;
  }
  .moviePoster {
    height: 25vh;
    position: relative;
    margin-top: -23vh;
    -o-object-fit: contain;
    object-fit: contain;
    border-radius: 10px;
    width: auto;
  }
  .trailers {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    scroll-behavior: smooth;
    overflow-x: scroll;
  }
  .TrailerThumb {
    border-radius: 10px;
    position: static;
  }
  .Slides {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .centerItem {
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
  }
}

