@media (max-width: 768px) {
  .swiper-container {
    margin-top: 1vh;
    margin-bottom: 1vh;
    width: 100%;
    height: 43vmax;
    overflow-x: scroll;
    display: flex;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    white-space: nowrap;
  }

  .swiper-slide {
    width: 100%;
    border-radius: 10px;
    flex-shrink: 0;
    height: 100%;
    scroll-snap-align: start;
    padding: 10px;
    shape-rendering: auto;
  }

  .SlideImg {
    border-radius: 10px;
    width: 100%;
    height: auto;
    width: 100%;
    height: 30vmax;
    object-fit: cover;
  }

  .SlideSubTitle {
    text-align: center;
    overflow-x: scroll;
    /* padding: 0%; */
    /* padding: 1vh; */
  }

  .SlideSubTitleText {
    display: inline;
    font-weight: bold;
    font-size: 3vh;
  }

  .SlideSubTitle::-webkit-scrollbar,
  .SlideSubTitleText::-webkit-scrollbar,
  .swiper-container::-webkit-scrollbar {
    display: none;
  }
}

